* {
  margin: 0;
  padding: 0;
}
h1,h2,h3,h4,h5,p {
  margin: 0;
  padding: 0;
}
button,input {
  outline: none;
  border: none;
  background: transparent;
}
button {
  cursor: pointer;
}
a {
  text-decoration: none;
  color: inherit;
}


@font-face {
  font-family: 'YanoneKaffeesatzVariableFont';
  src: url('./fonts/YanoneKaffeesatzVariableFont_wght.eot');
  src: url('./fonts/YanoneKaffeesatzVariableFont_wght.eot') format('embedded-opentype'),
       url('./fonts/YanoneKaffeesatzVariableFont_wght.woff2') format('woff2'),
       url('./fonts/YanoneKaffeesatzVariableFont_wght.woff') format('woff'),
       url('./fonts/YanoneKaffeesatzVariableFont_wght.ttf') format('truetype'),
       url('./fonts/YanoneKaffeesatzVariableFont_wght.svg#YanoneKaffeesatzVariableFont_wght') format('svg');
}
